//
// Animations
// ====================================


.pulse-heart {
  animation: pulse-heart 1s infinite;
}


//
// keyframes
// #
@keyframes pulse-heart {

  0% {
    transform : scale(1);
  }

  15% {
    transform : scale(1.2);
  }

  25% {
    transform : scale(1);
  }

  50% {
    transform : scale(1.2);
  }

  100% {
    transform : scale(1.2);
  }
}

@keyframes cat-bounce-head {
  100% {
    transform: translateY(-4%);
  }

  75% {
    transform: translateY(-2%);
  }

  50% {
    transform: translateY(-1%);
  }

  25% {
    transform: translateY(-0.5%);
  }

  0% {
    transform: translateY(0);
  }
}
